<template>
  <div class="label">:</div>
  <div class="content">
    <Select :options="options" @valueChanged="change" :value="globalValue" />
  </div>
  <div class="clear"></div>
</template>

<script>
import { useStore } from "vuex";
import { computed } from "@vue/runtime-core";
import Select from "@/pages/internal/views/providerTools/activityCreateForm/inputs/Select.vue";

export default {
  name: "LanguageSelect",
  setup(props, context) {
    const store = useStore();
    store.commit("productPage/setPayload", { language: { value: [""] } });
    const globalValue = computed({
      get() {
        let entity = store.state.productPage.payload["language"];
        if (entity) return entity.value;
        return ["", ""];
      },
      set(val) {
        let payload = {};
        payload["language"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });
    return {
      globalValue,
    };
  },
  data() {
    return {
      options: [
        { name: "()", value: "" },
        { name: "", value: "" },
        { name: "", value: "" },
        { name: "", value: "" },
        { name: "", value: "" },
        { name: "", value: "" },
        { name: "", value: "" },
        { name: "()", value: "" },
        { name: "", value: "" },
        { name: "", value: "" },
        { name: "", value: "" },
        { name: "", value: "" },
        { name: "", value: "" },
      ],
    };
  },
  methods: {
    change(val) {
      this.globalValue = val;
    },
  },
  components: {
    Select,
  },
};
</script>

<style scoped></style>
