<script>
import { computed, reactive, ref, toRaw } from '@vue/runtime-core'
import { useStore } from 'vuex'
import PopInfoBlack from '@/pages/internal/views/providerTools/activityCreateForm/inputs/popInfoBlack'
import FieldStructure from './FieldStructure.vue'
import request from '@/pages/internal/network/request'
import { watchEffect } from 'vue'
export default {
  name: 'insurceSelect2',
  components: {
    FieldStructure,
    PopInfoBlack
  },
  props: {
    field: Object
  },
  setup(props, context) {
    // console.log(props)
    const isShow = ref(false)
    const isShow2 = ref(false)
    const isShow3 = ref(false)
    const showTxar2 = ref(false)
    const showTxar3 = ref(false)
    const arr = [
      { name: '()5' },
      { name: '()10' },
      { name: '()20' },
      { name: '()30' },
      { name: '()10' },
      { name: '()20' },
      { name: '()30' }
    ]
    const getCurrentItem = ref('')
    const getCurrentItem2 = ref('')
    const txpl =
      '2/1011000100'
    const store = useStore()
    const { charLimit, fieldTitle, style, required, inputid, first, second } =
      toRaw(props.field)

    const selected = reactive({
      one: false,
      two: false
    })
    watchEffect(() => {
      if (
        store.state.productPage.payload.accident_insurance_self_details?.value
      ) {
        isShow3.value = true
        getCurrentItem2.value =
          store.state.productPage.payload.accident_insurance_self_details.value
        getCurrentItem.value = ''
      }
    })
    // store.commit("productPage/setPayload", {
    //   [first]: {
    //     value: false,
    //   },
    // });
    // store.commit("productPage/setPayload", {
    //   [second]: {
    //     value: "",
    //   },
    // });
    // store.commit("productPage/setPayload", {
    //   ["accident_insurance_self_details"]: {
    //     value: "()5",
    //   },
    // });
    const txarvalue = computed({
      get() {
        let value = store.state.productPage.payload[second]
        if (!value) {
          // store.commit("productPage/setPayload", {
          //   [second]: {
          //     value: "",
          //   },
          // });
          return txpl
        }
        return value.value
      },
      set(val) {
        updateDetail(val)
      }
    })
    const showTxar = computed({
      get() {
        let value = store.state.productPage.payload[first]
        if (!value) {
          // store.commit("productPage/setPayload", {
          //   [first]: {
          //     value: false,
          //   },
          // });
          return false
        }
        return value.value
      },
      set(val) {
        updateDetail(val)
      }
    })
    const show = e => {
      // console.log(e)
      updateType(e)
    }
    const show2 = item => {
      getCurrentItem2.value = item.name
      store.commit('productPage/setPayload', {
        ['accident_insurance_self_details']: {
          value: item.name
        }
      })
      isShow2.value = false
      showTxar3.value = true
    }
    const ShowSelect = () => {
      isShow.value = true
    }
    const cancel = () => {
      showTxar2.value = false
    }
    const cancel2 = () => {
      showTxar3.value = false
    }
    const ShowSelect2 = () => {
      isShow2.value = true
    }
    const updateType = e => {
      if (e == 2) {
        getCurrentItem.value = ''
        isShow3.value = false
        showTxar2.value = true
        store.commit('productPage/setPayload', {
          ['accident_insurance_self_details']: {
            value: ''
          }
        })
      }
      if (e == 1) {
        getCurrentItem.value = ''
        isShow3.value = true
        store.commit('productPage/setPayload', {
          ['accident_insurance_self_details']: undefined
        })
        if (getCurrentItem2.value !== '') {
          store.commit('productPage/setPayload', {
            ['accident_insurance_self_details']: {
              value: getCurrentItem2.value
            }
          })
        }
        console.log(
          store.state.productPage.payload['accident_insurance_self_details']
        )
      }
      store.commit('productPage/setPayload', {
        [first]: {
          value: Boolean(e - 1)
        }
      })
      isShow.value = false
      console.log(
        store.state.productPage.payload['accident_insurance_self_details']
      )
      console.log(
        store.state.productPage.payload['liability_insurance_self_details']
      )
    }
    const updateDetail = e => {
      store.commit('productPage/setPayload', {
        [second]: {
          value: e
        }
      })
    }
    const choosefile = e => {
      document.getElementById(inputid).click()
    }
    return {
      ShowSelect,
      ShowSelect2,
      isShow,
      isShow2,
      isShow3,
      cancel,
      cancel2,
      getCurrentItem,
      getCurrentItem2,
      charLimit,
      fieldTitle,
      style,
      required,
      selected,
      show,
      show2,
      inputid,
      choosefile,
      showTxar,
      showTxar2,
      showTxar3,
      txpl,
      txarvalue,
      first,
      second,
      arr
    }
  },
  watch() {},
  methods: {
    init() {
      request({
        method: 'post',
        url: '/api/e9b849a515a84327b424af7ccdbf2949/v1_0_0/product/get',
        data: {
          id: useStore().state.productPage.id
        },
        auth: {
          username:
            'NginxAuthUsername16381efa069a90cbde99854c81183036a4bd2e4776eb770fa69740f1c5c06aa4',
          password:
            'NginxAuthPassworde0588e3b3c5b240fe3f5fb653d2990f34b52ee0b595acf6553a924b0716cb366'
        },
        headers: {
          'Content-Type': 'application/json'
        }
      }).then(res => {
        if ((res.Code = 200)) {
          // 
          if (res.details?.accident_insurance_self_if) {
            this.getCurrentItem = ''
          }
          // 
          if (res.details.accident_insurance_self_details.value) {
            this.getCurrentItem = ''
            this.getCurrentItem2 = res.details.accident_insurance_self_details
            this.isShow3 = true
          }
          // 
        } else {
        }
      })
    }
  },
  mounted() {
    // 
    this.init()
  },
  data() {
    return {
      ifShowPop: false
    }
  }
}
</script>

