<template>
  <div class="label">:</div>
  <div class="content">
    <!-- <select v-model="globalValue">
            <option value="" disabled selected style="display: none"></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
        </select> -->
    <SelectInput
      :List="selectList"
      :value="globalValue"
      @sendValue="changeType"
      title=""
    ></SelectInput>
  </div>
  <div class="clear"></div>
</template>

<script>
import { useStore } from "vuex";
import { computed } from "@vue/runtime-core";
import Select from "@/pages/internal/views/providerTools/activityCreateForm/inputs/Select.vue";
import SelectInput from "../../../inputs/SelectInput.vue";
export default {
  name: "ActivityType",
  setup(props, context) {
    const store = useStore();
    const globalValue = computed({
      get() {
        let value = store.state.productPage.payload["activity_type"];
        if (value) return value.value;
        return "";
      },
      set(val) {
        let payload = {};
        payload["activity_type"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });
    const changeType = (e) => {
      globalValue._setter(e);
    };
    return {
      globalValue,
      changeType,
    };
  },
  data() {
    return {
      selectList: [
        { value: "", option: "" },
        { value: "", option: "" },
        { value: "", option: "" },
        { value: "", option: "" },
        { value: "", option: "" },
        { value: "", option: "" },
        { value: "", option: "" },
      ],
    };
  },
  methods: {},
  components: {
    Select,
    SelectInput,
  },
};
</script>

