  <div class="label">:</div>
  <div class="content">
    <span></span>
    <textarea
      rows="6"
      placeholder="......"
      v-model="include_item"
    ></textarea>
    <div
      style="top: 35px"
      class="img"
      :class="ifactived1 ? 'actived' : 'unactived'"
      @click="ifactived1 = !ifactived1"
    >
      <pop-info
        :font_size="0.9"
        :top="-25"
        :left="30"
        @click.stop=""
        v-show="ifactived1"
      >
        <h3></h3>
        X
        </pop-info
      >
    </div>

    <span></span>
    <textarea
      rows="8"
      placeholder="......"
      v-model="exclude_item"
    ></textarea>
    <div
      style="top: 250px"
      class="img"
      :class="ifactived2 ? 'actived' : 'unactived'"
      @click="ifactived2 = !ifactived2"
    >
      <pop-info
        :font_size="0.9"
        :top="-25"
        :left="30"
        @click.stop=""
        v-show="ifactived2"
      >
        <h3></h3>
        1.
        2.
        3.XSPA
        4.</pop-info
      >
    </div>

    <span></span>
    <textarea
      rows="8"
      placeholder="......"
      v-model="insurance_info"
    ></textarea>
    <div
      style="top: 510px"
      class="img"
      :class="ifactived3 ? 'actived' : 'unactived'"
      @click="ifactived3 = !ifactived3"
    >
      <pop-info :font_size="0.9" :top="-25" :left="30" v-show="ifactived3">
        <h3></h3>
        10/
        
        10521
        210
        </pop-info
      >
    </div>

    <span></span>
    <div class="last" disabled rows="6">
      
    </div>
    <!-- <label for="agreed"><input id="agreed" type="radio" value="true" v-model="agreed" @click.stop="changeRadio"> </label> -->
  </div>
  <div class="clear"></div>
</template>

<script>
import { useStore } from "vuex";
import { computed } from "@vue/runtime-core";
import popInfo from "@/pages/internal/views/providerTools/activityCreateForm/inputs/popInfo";

export default {
  name: "PriceDesc",
  setup(props, context) {
    const store = useStore();
    const include_item = computed({
      get() {
        let entity = store.state.productPage.payload["price_include"];
        if (entity) return entity.value;
        return "";
      },
      set(val) {
        let payload = {};
        payload["price_include"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });

    const exclude_item = computed({
      get() {
        let entity = store.state.productPage.payload["price_exclude"];
        if (entity) return entity.value;
        return "";
      },
      set(val) {
        let payload = {};
        payload["price_exclude"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });

    const insurance_info = computed({
      get() {
        let entity = store.state.productPage.payload["price_insurance_detail"];
        if (entity) return entity.value;
        return "";
      },
      set(val) {
        let payload = {};
        payload["price_insurance_detail"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });
    return {
      include_item,
      exclude_item,
      insurance_info,
    };
  },
  data() {
    return {
      ifactived1: false,
      ifactived2: false,
      ifactived3: false,
      agreed: true,
    };
  },
  methods: {
    changeRadio() {
      this.agreed = !this.agreed;
    },
  },
  components: {
    popInfo,
  },
};
</script>

