<template>
  <div class="label">:</div>
  <div class="content">
    <!-- <select v-model="if_stay_out">
            <option value="" disabled selected style="display: none"></option>
            <option value="true"></option>
            <option value="false"></option>
        </select> -->
    <SelectInput
      :List="selectList"
      :value="if_stay_out"
      @sendValue="changeStay"
      title=""
    ></SelectInput>
    <SelectInput
      :List="selectActivityFromList"
      :value="activity_form"
      @sendValue="changeFrom"
      title=""
    ></SelectInput>
    <!-- <select v-model="activity_form">
            <option value="" disabled selected style="display: none"></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
        </select> -->
    <div
      class="img"
      @mouseenter="ifShowPop = true"
      @mouseleave="ifShowPop = false"
    ></div>
    <pop-info-black :left="360" :top="-15" v-show="ifShowPop"
      > 
      </pop-info-black
    >
  </div>
  <div class="clear"></div>
</template>

<script>
import { useStore } from "vuex";
import { toRefs } from "vue";
import { computed } from "@vue/runtime-core";
import PopInfoBlack from "@/pages/internal/views/providerTools/activityCreateForm/inputs/popInfoBlack";
import SelectInput from "../../../inputs/SelectInput.vue";

export default {
  name: "ActivityForm",
  components: { PopInfoBlack, SelectInput },
  setup(props, context) {
    const store = useStore();
    const if_stay_out = computed({
      get() {
        let value = store.state.productPage.payload["stay_if"];
        if (value) return value.value;
        return "";
      },
      set(val) {
        let payload = {};
        payload["stay_if"] = { value: val };
        if (val == "true") {
          payload.stay_if.value = true;
        } else {
          payload.stay_if.value = false;
        }
        store.commit("productPage/setPayload", payload);
      },
    });
    const activity_form = computed({
      get() {
        let value = store.state.productPage.payload["activity_mode"];
        if (value) return value.value;
        return "";
      },
      set(val) {
        let payload = {};
        payload["activity_mode"] = { value: val };
        store.commit("productPage/setPayload", payload);
      },
    });
    const changeStay = (e) => {
      if_stay_out._setter(e);
    };
    const changeFrom = (e) => {
      activity_form._setter(e);
    };
    return {
      if_stay_out,
      activity_form,
      changeStay,
      changeFrom,
    };
  },
  data() {
    return {
      ifShowPop: false,
      selectList: [
        { value: true, option: "" },
        { value: false, option: "" },
      ],
      selectActivityFromList: [
        { value: "", option: "" },
        { value: "", option: "" },
        { value: "", option: "" },
      ],
    };
  },
  methods: {
    test() {
      alert(1);
    },
  },
};
</script>

